import axios from "axios";
import jwt_decode from "jwt-decode";
import setAuthToken from "../utils/setAuthToken";
import { setCurrentUser } from './../actions/authActions'
import {  makeAdminAction, removeAdminAction } from './../actions/adminAction'
import {
  GET_ERRORS,
} from "./../actions/types";
// Register User
export const registerUser = (userData, history) => dispatch => {
  axios
    .post("/api/users/register", userData)
    .then(res => history.push("/login")) // re-direct to login on successful register
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};
// Login - get user token
export const loginUser = userData => dispatch => {
  axios
    .post("/api/users/login", userData)
    .then(res => {
      // Save to localStorage
// Set token to localStorage
      const { token } = res.data;
      localStorage.setItem("jwtToken1", token);
      // Set token to Auth header
      setAuthToken(token);
      // Decode token to get user data
      const decoded = jwt_decode(token);
      // Set current user
      dispatch(setCurrentUser(decoded));
    })
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};
// Log user out
export const logoutUser = () => dispatch => {
  // Remove token from local storage
  localStorage.removeItem("jwtToken1");
  // Remove auth header for future requests
  setAuthToken(false);
  // Set current user to empty object {} which will set isAuthenticated to false
  dispatch(setCurrentUser({}));
};
export const makeAdmin = (data) => dispatch =>{
  axios
    .post("/api/users/makeAdmin", data)
    .then(res =>{
      dispatch(makeAdminAction(res.data))
    })
};
export const removeAdmin = (data) => dispatch =>{
  axios
    .post("/api/users/removeAdmin", data)
    .then(res =>{
      dispatch(removeAdminAction(res.data))
    })
};

export const changePasswordApi = (data, history) => dispatch =>{
  axios
    .post("/api/users/changePassword", data)
    .then(res => history.push("/login"))
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};

